import React from 'react';
import PropTypes from 'prop-types';

import { Modal } from '../Modal';
import { Share } from '../Share';

export const ShareModal = ({ url = document.location.href, ...rest }) => (
    <Modal {...rest}>
        <Share link={url} />
    </Modal>
);

ShareModal.propTypes = {
    url: PropTypes.string,
};
